import Vue from 'vue'
import Router from 'vue-router'
import MenuInicial from '@/views/MenuInicial'
import Login from '@/views/Login'
import Mensalidade from '@/views/Mensalidade'
import Pagamento from '@/views/Pagamento'
import OpcaoPagamento from '@/views/OpcaoPagamento'
import Requerimento from '@/views/Requerimento'
import OpcaoBoleto from '@/views/OpcaoBoleto'
import Estudante from '@/views/Estudante'
import Boleto from '@/views/Boleto'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MenuInicial',
      component: MenuInicial
    },
    {
      path: '/Login',
      name: 'Login',
      component: Login
    },
    {
      path: '/Mensalidade',
      name: 'Mensalidade',
      component: Mensalidade
    },
    {
      path: '/Pagamento',
      name: 'Pagamento',
      component: Pagamento
    },
    {
      path: '/OpcaoPagamento',
      name: 'OpcaoPagamento',
      component: OpcaoPagamento
    },
    {
      path: '/Requerimento',
      name: 'Requerimento',
      component: Requerimento
    },
    {
      path: '/OpcaoBoleto',
      name: 'OpcaoBoleto',
      component: OpcaoBoleto
    },
    {
      path: '/Estudante',
      name: 'Estudante',
      component: Estudante
    },
    {
      path: '/Boleto',
      name: 'Boleto',
      component: Boleto
    }
  ]
})



