import { MonthlyPaymentsService, BilletService } from "../../common/service/api";
import {
  FETCH_MONTHLY_PAYMENTS,
  UPDATE_MONTHLY_PAYMENT,
  UPDATE_PAYMENT,
  UPDATE_LINE,
  SEND_EMAIL
} from "../action-type";
import { 
  SET_MONTHLY_PAYMENTS, 
  SET_PAYMENT,
  SET_LINE
} from "../mutation-types";

const initialState = {
  mensalidade: {
    pagamentoRealizado: false,
    valor:0,
  },
  linhaDigitavel:"",
  mensalidades: []
};

export const state = { ...initialState };

export const actions = {
  async [FETCH_MONTHLY_PAYMENTS](context, matricula) {
    const { data } = await MonthlyPaymentsService.get(matricula);
    context.commit(SET_MONTHLY_PAYMENTS, data);
    return data;
  },
  async [UPDATE_MONTHLY_PAYMENT](context, payload) {
    await MonthlyPaymentsService.update(context.getters.mensalidade);
    context.dispatch(FETCH_MONTHLY_PAYMENTS, payload);
  },
  async [UPDATE_PAYMENT](context, payload) {
    context.commit(SET_PAYMENT, payload);
    return payload;
  },
  async [UPDATE_LINE](context, payload) {
    const boleto = {
      matricula: payload,
      valorPagamento: this.state.monthlyPayment.mensalidade.valorLiquido
    }
    const { data } = await BilletService.dadosBoleto(boleto);    
    context.commit(SET_LINE, data);
    return data;
  },
  async [SEND_EMAIL](context, payload){
    const boleto = {
      matricula: payload,
      valorPagamento: this.state.monthlyPayment.mensalidade.valorLiquido
    }
    await BilletService.sendEmail(boleto);    
  }
};
export const mutations = {
  [SET_MONTHLY_PAYMENTS](state, mensalidades) {
    state.mensalidades = mensalidades;
  },
  [SET_PAYMENT](state, mensalidade) {
    state.mensalidade = mensalidade;
  },
  [SET_LINE](state, linhaDigitavel ) {
    state.linhaDigitavel = linhaDigitavel;
  }
};
const getters = {
  mensalidades(state) {
    return state.mensalidades;
  },
  mensalidade(state) {
    return state.mensalidade;
  },
  linhaDigitavel(state) {
    return state.linhaDigitavel;
  }
};

export default {
  state,
  actions,
  mutations,
  getters
};



