<template>
     <div class="middle">
        <a class="btnPrincipal " id="btn0" v-on:click="opcaoBoleto" v-b-modal.boleto>
            <i class="fa fa-barcode"></i><br>
            <span>Boleto</span>
        </a>
        <a class="btnPrincipal" id="btn2" v-b-modal.pagamento>
            <i class="fa fa-credit-card"></i><br>
            <span>Cartão</span>
        </a>
    <div class="button">
    <router-link :to="{name:'Mensalidade'}">
        <a class="botao" id="btn1">
            <i class="fa fa-angle-left"></i><br>
            <span>Voltar</span><br>
        </a>
    </router-link>
    </div>
    <pagamento></pagamento>
    <boleto></boleto>
</div>
</template>
<script>
import {
  UPDATE_LINE
} from "./../store/action-type";
import { mapGetters } from 'vuex';
import router from '../router';
import Pagamento from '../views/Pagamento';
import Boleto from '../views/OpcaoBoleto';
export default {
  name: 'OpcaoPagamento',
  data: () => ({}),
  mounted(){
      if(!this.isLoggedIn){
          return router.push('/Login');
      }
  },
  computed: {
    ...mapGetters(["isLoggedIn"])
  },
  methods: {
      opcaoBoleto(){
          this.$store.dispatch(UPDATE_LINE, this.$store.state.authorization.aluno.matricula);
    }
  },
  components: {
      'pagamento': Pagamento,
      'boleto': Boleto
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
body{
    padding: 0;
    margin: 0;
}
.middle{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
}
img{
    width: 100px;
    height: 60px;
    position: fixed;
    left: 703px;
    bottom: 30px; 

}
.btnPrincipal {
    display: inline-block;
    width: 200px;
    height: 200px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 20%;
    box-shadow: 0 5px 15px -5px #00000070;
    overflow: hidden;
    position: relative;
    text-decoration:none;
}
.btnPrincipal span {
    font-family: sans-serif;
    color: #fff;
    text-decoration:none;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.btnPrincipal  i{
    color: #fff;
    line-height: 320%;
    font-size: 40px;
    transition: 0.2s linear;
}
.btnPrincipal :active{
    transform: translate(2px, 2px);
}
#btn0{
    background: #7f8c8d;
}
#btn2{
    background: #7dc400;
}
.button{
    position: absolute;
    top: 130%;
    transform: translateY(-50%);
    width: 100%;
    text-align: center;
    text-decoration: none;
}
.botao{
    display: inline-block;
    width: 100px;
    height: 100px;
    background: #f1f1f1;
    margin: 10px;
    border-radius: 20%;
    box-shadow: 0 5px 15px -5px #00000070;
    overflow: hidden;
    position: relative;
    text-decoration:none;
}
.botao span {
    font-family: sans-serif;
    color:  #7dc400;
    text-decoration:none;
    text-transform: uppercase;
    letter-spacing: 2px;
}
.botao i{
    color:  #7dc400;
    line-height: 320%;
    font-size: 20px;
    transition: 0.2s linear;
}
.botao:active{
    transform: translate(2px, 2px);
}
</style>



