<template>
  <div>
    <b-modal
    id="requerimento"
      size="lg"
      title="Requerimento"
      :header-bg-variant="headerRequerimento"
      v-model="show" >
      <div class="row">
        <div class="form-group col-md-10">
          <label for="solicitacao" class="form-label">Solicitação</label>
          <v-select
            :options="requerimentos"
            label="tipo"
            v-model="solicitacao"
            @input="setSelected"
            required
            placeholder="Selecione a solicitação"
          ></v-select>
        </div>
        <div class="form-group col-md-6">
          <label for="curso" class="form-label">Curso</label>
          <input type v-model="aluno.curso" readonly name="curso" id="curso" class="form-control">
        </div>
        <div class="form-group col-md-4">
          <label for="periodo" class="form-label">Periodo letivo</label>
          <input
            type
            name="periodo"
            v-model="periodo"
            placeholder="AAAA/S"
            id="periodo"
            readonly
            class="form-control"
          >
        </div>
        <div class="form-group col-md-4">
          <label for="prazo" class="form-label">
            Prazo de
            solicitação
          </label>
          <input type id="prazo" v-model="prazoSolicitacao" class="form-control" readonly>
        </div>
        <div class="form-group col-md-3">
          <label for="entrega" class="form-label">Prazo de entrega</label>
          <input type id="entrega" v-model="prazoEntrega" class="form-control" readonly>
        </div>
        <div class="form-group col-md-3">
          <label for="entrega" class="form-label">Valor</label>
          <input type id="entrega" v-model="valor" class="form-control" readonly>
        </div>
      </div>
       <div slot="modal-footer" class="w-100">
            <b-button variant="success" size="md"  @click="solicitarRequerimento">
              Solicitar
            </b-button>
            <b-button variant="secondary" size="md"  @click="show=false">
              Cancelar
            </b-button>
      </div>
    </b-modal>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import router from "../router";
import { FETCH_REQUEST } from "../store/action-type";
var dt = new Date();
var semestre = dt.getMonth() <= 6 ? 1 : 2;
export default {
  name: "Pagamento",
  data: () => ({
    solicitacao: null,
    prazoSolicitacao: null,
    prazoEntrega: null,
    valor: null,
    periodo: dt.getFullYear() + "/" + semestre,
    errors: [], 
    show: false,
    headerRequerimento: "orange",
  }),
  mounted() {
    if (!this.isLoggedIn) {
      return router.push("/Login");
    }
    this.fetchRequest();
  },
  computed: {
    ...mapGetters(["isLoggedIn", "mensalidade", "aluno", "requerimentos"])
  },
  methods: {
    fetchRequest() {
      this.$store.dispatch(FETCH_REQUEST);
    },
    setSelected(value) {
      this.prazoSolicitacao = value.prazoSolicitacao;
      this.prazoEntrega = value.prazoEntrega;
      this.valor = value.valor == null ? "Não cobrado" : value.valor;
    },
    solicitarRequerimento(e) {
      if (this.solicitacao) {
        alert(
          "Solicatação realizada com sucesso!\n encaminhe-se a secretaria no prazo " +
            this.prazoEntrega
        );
        this.prazoSolicitacao = null;
        this.prazoEntrega = null;
        this.valor = null;
        this.solicitacao = null;
        return true;
      }
      if (!this.solicitacao) {
        alert("selecione uma solicitação");
      }
      e.preventDefault();
    }
  }
};
</script>
<style>
  .bg-orange {
    background-color: #ff9900;
    color:white;
  }
</style>



