<template>
<div>
<b-modal id="boleto" size="md" title="Boleto" :header-bg-variant="headerBoleto" v-model="show">
    <div class="middle">
        <div style="font-size:25px;">
            <i>Parcela: <b style="font-size:25px;"> {{mensalidade.parcela}}</b></i>
        </div>
        <div style="font-size:25px;">
            <i>Valor: <b style="font-size:25px;">R$ {{mensalidade.valorAtual}}</b></i>
        </div>
        <br>
        <div><i>Linha digitavel:<br><b>{{linhaDigitavel}}</b></i></div>
        <br>
        <b-button variant="primary" size="md" v-on:click="envioEmail"><i class="fa fa-envelope"></i><br><span>Enviar por Email</span></b-button>
    </div>
    <div slot="modal-footer" class="w-100">
            <b-button variant="secondary" size="md"  @click="show=false">
              Fechar
            </b-button>
      </div>
</b-modal>
</div>
</template>
<script>
import { mapGetters } from 'vuex';
import router from '../router'
import {
  SEND_EMAIL
} from "./../store/action-type";
export default {
  name: 'OpcaoPagamento',
  data: () => ({
      headerBoleto:'grey',
      show: false
  }),
  mounted(){
      if(!this.isLoggedIn){
          return router.push('/Login');
      }
  },
  computed: {
    ...mapGetters(["isLoggedIn", "linhaDigitavel", "mensalidade"])
  },
  methods: {
    envioEmail(){
          this.$store.dispatch(SEND_EMAIL, this.$store.state.authorization.aluno.matricula);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.middle{
    text-align: center;
}
.bg-grey {
    background: #7f8c8d;
    color: white;
}
</style>



