import { RequestService} from "../../common/service/api";
import {
  FETCH_REQUEST,
} from "../action-type";
import { 
  SET_REQUEST,
} from "../mutation-types";

const initialState = {
  requerimento: {
    tipo: null,
    valor:null,
    prazo:null,
    prazoSolicitacao:null
  },
  requerimentos: []
};

export const state = { ...initialState };

export const actions = {
  async [FETCH_REQUEST](context, matricula) {
    const { data } = await RequestService.get(matricula);
    context.commit(SET_REQUEST, data);
    return data;
  }
};
export const mutations = {
  [SET_REQUEST](state, requerimentos) {
    state.requerimentos = requerimentos;
  },
};
const getters = {
  requerimentos(state) {
    return state.requerimentos;
  },
  requerimento(state) {
    return state.requerimento;
  }
};

export default {
  state,
  actions,
  mutations,
  getters
};



