import { AuthorizationService } from "../../common/service/api";
import {
  AUTHORIZATION,
  LOGOUT,
  LOGIN
} from "../action-type";
import { SET_AUTH } from "../mutation-types";
import router from "../../router";

const initialState = {
  aluno: {
    matricula: "",
    password: "",
    token:null,
  },
  loginError:null,
};

export const state = { ...initialState };

export const actions = {  
  async [AUTHORIZATION](context, payload) {
    const { data }  = await AuthorizationService.auth(payload);
    const alunoTemp = {
      nome: data.aluno[0].nome,
      matricula: data.aluno[0].matricula,
      email: data.aluno[0].email,
      curso: data.aluno[0].curso.nomeCurso,
      cpf: data.aluno[0].cpf,
      periodo: data.aluno[0].periodo,
      sala: data.aluno[0].sala,
      token: data.token.token  
    }
    context.dispatch(LOGIN, alunoTemp);
    return data;
  },
  async [LOGIN](context, aluno) {
    context.commit(SET_AUTH, aluno);
    router.push('/');
  },
  async [LOGOUT](context) {
    context.commit(SET_AUTH, initialState);
    router.push('/Login');
  },
};
export const mutations = {
  [SET_AUTH](state, aluno) {
    state.aluno = aluno;
  }
};
const getters = {
    isLoggedIn(state) {
      return !!state.aluno.token;
    },
    aluno(state){
      return state.aluno;
    }
};
export default {
  state,
  actions,
  mutations,
  getters
};



