<template>
  <div>
    <b-modal id="estudante" size="lg" 
        title="Estudante" 
        :header-bg-variant="headerEstudante" 
        v-model="show">
        <div class="row">
          <div class="form-group col-md-4">
            <label for="matricula" class="form-label">Matrícula</label>
            <input
              type
              v-model="aluno.matricula"
              name="matricula"
              placeholder
              id="matricula"
              class="form-control"
              readonly
            >
          </div>
          <div class="form-group col-md-6">
            <label for="nome" class="form-label">Nome</label>
            <input type v-model="aluno.nome" name="nome" id="nome" class="form-control" readonly>
          </div>
          <div class="form-group col-md-4">
            <label for="email" class="form-label">E-mail</label>
            <input
              type="email"
              v-model="aluno.email"
              name="email"
              placeholder
              id="email"
              class="form-control"
              readonly
            >
          </div>
          <div class="form-group col-md-6">
            <label for="curso" class="form-label">Curso</label>
            <input
              type="text"
              v-model="aluno.curso"
              name="curso"
              id="curso"
              class="form-control"
              readonly
            >
          </div>
          <div class="form-group col-md-4">
            <label for="curso" class="form-label">Período</label>
            <input
              type="text"
              v-model="aluno.periodo"
              name="curso"
              id="curso"
              class="form-control"
              readonly
            >
          </div>
          <div class="form-group col-md-2">
            <label for="curso" class="form-label">Sala</label>
            <input
              type="text"
              v-model="aluno.sala"
              name="curso"
              id="curso"
              class="form-control"
              readonly
            >
          </div>
        </div>
        <div slot="modal-footer" class="w-100">
            <b-button variant="primary" size="md" @click="show=false">
              Fechar
            </b-button>
      </div>
    </b-modal>
  </div>
</template>
<script>
import { FETCH_MONTHLY_PAYMENTS } from "./../store/action-type";
import { mapGetters } from "vuex";
import router from "../router";
export default {
  name: "Mendalidade",
  data: () => ({
      headerEstudante: "purple",
      show: false
  }),
  mounted() {
    if (!this.isLoggedIn) {
      return router.push("/Login");
    }
  },
  computed: {
    ...mapGetters(["isLoggedIn", "aluno"])
  },
  methods: {}
};
</script>
<style>
.bg-purple {
  background-color: #7d3f98;
  color: white;
}
</style>




