import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import { API_URL } from "./config";

const ApiService = {
  init() {
    Vue.use(VueAxios, axios);
    axios.defaults.baseURL = API_URL;
  },

  setHeader() {
    Vue.axios.defaults.headers.common[
      "Authorization"
    ] = `Bearer ${store.state.authentication.aluno.token}`;
  },

  query(resource, params) {
    return Vue.axios.get(resource, params).catch(error => {
      throw new Error(`[RWV] ApiService ${error}`);
    });
  },

  get(resource) {
    return Vue.axios.get(`${resource}`).catch(error => {
      throw new Error(`${error}`);
    });
  },

  post(resource, params) {
    return Vue.axios.post(`${resource}`, params);
  },
  postWithParams(resource, params) {
    return Vue.axios.post(`${resource}/${params.matricula}`, params);
  },  
  show(resource, params) {
    return Vue.axios.get(`${resource}/${params}`, params);
  },

  update(resource, params) {
    return Vue.axios.put(`${resource}/${params.id}`, params);
  },

  put(resource, params) {
    return Vue.axios.put(`${resource}`, params);
  },

  delete(resource) {
    return Vue.axios.delete(resource).catch(error => {
      throw new Error(`[RWV] ApiService ${error}`);
    });
  }
};

export default ApiService;

export const AuthorizationService = {
  auth(params){
    return ApiService.post('authenticate', params);
  },
  show(params){
    return ApiService.get('authenticate', params);
  }
};

export const MonthlyPaymentsService = {
  get(matricula) {
    return ApiService.show("mensalidade", matricula);
  },
  update(params) {
    return ApiService.update("mensalidade", params);
  },
};

export const BilletService = {
  dadosBoleto(boleto) {
    return ApiService.postWithParams("dadosBoleto", boleto);
  },
  sendEmail(boleto) {
    return ApiService.postWithParams("boleto/envioemail", boleto);
  },
};
export const RequestService = {
  get() {
    return ApiService.get("requerimento");
  },
};





