<template>
    <div class="loginbox">
            <img src="./../assets/facear.jpg" alt="logo" class="avatar">
            <form @submit="Login">
                <i class="fa fa-user"></i><input type="text" v-model="aluno.matricula" placeholder="Usuário">
                <br>
                <i class="fa fa-lock"></i><input type="password" v-model="aluno.password" placeholder="Senha">
                <input type="submit" value="Entrar">
            </form>
        </div>
</template>

<script>
import {
  AUTHORIZATION,
} from "./../store/action-type";
import { mapGetters } from "vuex";
export default {
  name: 'Login',
  data: () => ({
    aluno : {
      matricula: "",
      password: ""
    }
  }),
  methods: {
    Login(e) {
      e.preventDefault();
      this.$store.dispatch(AUTHORIZATION, this.aluno);
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.loginbox{
    width: 350px;
    height: 420px;
    background: #fff;
    color: #000;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    padding: 70px 30px;
}

.avatar{
    width: 200px;
    height: 135px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
}
.fa{
    color: #00572c;
}

.loginbox input[type="text"], input[type="password"]{
    width: 90%;
    margin-bottom: 20px;
    padding-left: 10px;
    border: none;
    border-bottom: 1px solid #00572c;
    background: transparent;
    outline: none;
    height: 40px;
    color: #000;
    font-size: 15px;
}

.loginbox input[type="text"]:hover{
    border-bottom: 1px solid #ff9900;
}

.loginbox input[type="password"]:hover{
    border-bottom: 1px solid #ff9900;
}

.loginbox input[type="submit"]{
    width: 100%;
    border: none;
    outline: none;
    height: 40px;
    background: #7dc300;
    box-shadow: -3px 3px 2px rgba(0,0,0,0.1);
    color: #fff;
    font-size: 18px;
}

.loginbox input[type="submit"]:active{
    cursor: pointer;
    background: #679e00;
    transform: translate(1px, 1px);
}
</style>



