<template>
  <div>
      <b-modal id="pagamento" size="lg" title="Pagamento" :header-bg-variant="headerPagamento" v-model="show">
      <div class="row">
        <div class="form-group col-md-6">
          <label for="nome-cartao" class="form-label">Nome do titular</label>
          <input
            type="text"
            v-model="nomeAluno"
            required
            v-uppercase
            placeholder="Igual ao descrito no cartão"
            class="form-control"
          >
        </div>
        <div class="form-group col-md-4">
          <label for="nome-bandeira" class="form-label">Operadora</label>
          <v-select
            :options="['MasterCard', 'Visa', 'Elo']"
            v-model="operadora"
            required
            placeholder="Selecione a operadora"
          ></v-select>
        </div>
        <div class="form-group col-md-6">
          <label for="num-cartao" class="form-label">Número do cartão</label>
          <input
            type
            v-model="numeroCartao"
            required
            v-mask="'#### #### #### ####'"
            placeholder="9999 9999 9999 9999"
            class="form-control"
          >
        </div>
        <div class="form-group col-md-4">
          <label for="validade" class="form-label">Data de validade</label>
          <input
            type="text"
            v-model="dataValidade"
            required
            v-mask="'##/##'"
            placeholder="MM/AA"
            class="form-control"
          >
        </div>
        <div class="form-group col-md-4">
          <label for="cvv" class="form-label">Código de segurança</label>
          <input
            type="password"
            v-model="codigoSeguranca"
            required
            name="cvv"
            placeholder="123"
            id="cvv"
            class="form-control"
          >
        </div>
      </div>
       <div style="font-size:20px;">
            <i>Parcela: <b style="font-size:25px;"> {{mensalidade.parcela}}</b></i>
        </div>
       <div style="font-size:20px;">
            <i>Valor: <b style="font-size:25px;">R$ {{mensalidade.valorAtual}}</b></i>
        </div>
      <div slot="modal-footer" class="w-100">
            <b-button variant="success" size="md"  @click="realizarPagamento()">
              Realizar pagamento
            </b-button>
            <b-button variant="secondary" size="md"  @click="show=false">
              Cancelar
            </b-button>
      </div>
    </b-modal>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
import router from "../router";
import { UPDATE_MONTHLY_PAYMENT } from "../store/action-type";
export default {
  name: "Pagamento",
  data: () => ({
    dataValidade: null,
    numeroCartao: null,
    nomeAluno: null,
    codigoSeguranca: null,
    operadora: null,
    errors: [],
    headerPagamento:'green',
    show: false
  }),
  mounted() {
    if (!this.isLoggedIn) {
      return router.push("/Login");
    }
  },
  computed: {
    ...mapGetters(["isLoggedIn", "mensalidade"])
  },
  methods: {
    realizarPagamento(e) {
      if (
        this.dataValidade &&
        this.numeroCartao &&
        this.operadora &&
        this.nomeAluno &&
        this.codigoSeguranca
      ) {
        this.$store.dispatch(
          UPDATE_MONTHLY_PAYMENT,
          this.$store.state.authorization.aluno.matricula
        );
        alert("Pagamento realizado com sucesso!");
        router.push("./Mensalidade");
        return true;
      }

      this.errors = [];

      if (!this.dataValidade) {
        this.errors.push("O data validade é obrigatório.");
      }
      if (!this.numeroCartao) {
        this.errors.push("o numero do cartão é obrigatório.");
      }

      if (!this.nomeAluno) {
        this.errors.push("O nome do titular é obrigatório.");
      }

      if (!this.operadora) {
        this.errors.push("Operadora é obrigatório.");
      }
      if (!this.codigoSeguranca) {
        this.errors.push("O codigo de segurança é obrigatório.");
      }
    }
  }
};
</script>

<style>
  .bg-green {
    background-color:  #7dc400;
    color:white;
  }
  .amount {
    font-size: 20px;
    color: blue
  }
</style>



